* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  line-height: 1.6;
}

.navbar {
  background: #1a1a1a;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

.navbar .logo {
  width: auto;
  height: 120px;
}

.nav-links {
  gap: 1.5rem;
  list-style: none;
  display: flex;
}

.nav-links a {
  color: #fff;
  font-weight: bold;
  text-decoration: none;
}

.nav-links a:hover {
  color: #0ab4ff;
}

.hero {
  color: #fff;
  text-align: center;
  background-image: url("coming-soon-bg.b6173654.jpg");
  background-position: center;
  background-size: cover;
  padding: 100px 20px;
  position: relative;
}

.hero:before {
  content: "";
  z-index: 1;
  background: #00000080;
  position: absolute;
  inset: 0;
}

.hero > * {
  z-index: 2;
  position: relative;
}

.hero h1 {
  margin-bottom: 1rem;
  font-size: 2.5rem;
}

.hero p {
  margin-bottom: 2rem;
  font-size: 1.2rem;
}

.cta-btn {
  color: #0ab4ff;
  background: #fff;
  border-radius: 5px;
  padding: .8rem 2rem;
  font-weight: bold;
  text-decoration: none;
}

.cta-btn:hover {
  color: #fff;
  background: #0ab4ff;
}

.section {
  text-align: center;
  max-width: 1000px;
  margin: auto;
  padding: 80px 20px;
}

.cards {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 1.5rem;
  margin-top: 2rem;
  display: flex;
}

.card {
  background: #f4f4f4;
  border-radius: 8px;
  flex: 250px;
  padding: 2rem;
  box-shadow: 0 2px 5px #0000001a;
}

.contact-section form {
  flex-direction: column;
  gap: 1rem;
  max-width: 500px;
  margin: auto;
  display: flex;
}

.contact-section input, .contact-section textarea, .contact-section button {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: .8rem;
}

.contact-section button {
  color: #fff;
  cursor: pointer;
  background: #0ab4ff;
  border: none;
  font-weight: bold;
}

.contact-section button:hover {
  background: #0077b3;
}

footer {
  text-align: center;
  color: #fff;
  background: #1a1a1a;
  padding: 2rem;
}

html, body {
  height: 100%;
  margin: 0;
  font-family: system-ui, Segoe UI, Roboto, Arial;
}

header {
  color: #fff;
  background: #036;
  align-items: center;
  gap: 12px;
  padding: 24px;
  display: flex;
}

#logo {
  object-fit: contain;
  width: 128px;
  height: 128px;
}

@media (width <= 600px) {
  .navbar .logo, #logo {
    width: auto;
    height: 64px;
  }
}

main {
  padding: 24px;
}
